  margin-inline: auto;
}

.nav > * a {
  font-weight: 600;
}

/*   --- root css --- */
:root {
  --font-size: 100%;
  --H1: 3.5rem;
  --H2: 1.125rem;
  --par: 1rem;
  --container: 90rem;
}

/*   --- normalize css --- */
html {
  -moz-tab-size: 4;
  tab-size: 4;
}

html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

body {
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
}

/**
1. Add the correct height in Firefox.
2. Correct the inheritance of border color in Firefox. (https://bugzilla.mozilla.org/show_bug.cgi?id=190655)
*/
hr {
  height: 0;
  /* 1 */
  color: inherit;
  /* 2 */
}

/*
Text-level semantics
====================
*/
/**
Add the correct text decoration in Chrome, Edge, and Safari.
*/
abbr[title] {
  text-decoration: underline dotted;
}

/**
Add the correct font weight in Edge and Safari.
*/
b,
strong {
  font-weight: bolder;
}

/**
1. Improve consistency of default fonts in all browsers. (https://github.com/sindresorhus/modern-normalize/issues/3)
2. Correct the odd 'em' font sizing in all browsers.
*/
code,
kbd,
samp,
pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, "Liberation Mono", Menlo, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

/**
Add the correct font size in all browsers.
*/
small {
  font-size: 80%;
}

/**
Prevent 'sub' and 'sup' elements from affecting the line height in all browsers.
*/
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/**
1. Remove text indentation from table contents in Chrome and Safari. (https://bugs.chromium.org/p/chromium/issues/detail?id=999088, https://bugs.webkit.org/show_bug.cgi?id=201297)
2. Correct table border color inheritance in all Chrome and Safari. (https://bugs.chromium.org/p/chromium/issues/detail?id=935729, https://bugs.webkit.org/show_bug.cgi?id=195016)
*/
table {
  text-indent: 0;
  /* 1 */
  border-color: inherit;
  /* 2 */
}

/*
Forms
=====
*/
/**
1. Change the font styles in all browsers.
2. Remove the margin in Firefox and Safari.
*/
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}

/**
Remove the inheritance of text transform in Edge and Firefox.
1. Remove the inheritance of text transform in Firefox.
*/
button,
select {
  /* 1 */
  text-transform: none;
}

/**
Correct the inability to style clickable types in iOS and Safari.
*/
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
Remove the inner border and padding in Firefox.
*/
::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
Restore the focus styles unset by the previous rule.
*/
:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
Remove the additional ':invalid' styles in Firefox.
See: https://github.com/mozilla/gecko-dev/blob/2f9eacd9d3d995c937b4251a5557d95d494c9be1/layout/style/res/forms.css#L728-L737
*/
:-moz-ui-invalid {
  box-shadow: none;
}

/**
Remove the padding so developers are not caught out when they zero out 'fieldset' elements in all browsers.
*/
legend {
  padding: 0;
}

/**
Add the correct vertical alignment in Chrome and Firefox.
*/
progress {
  vertical-align: baseline;
}

/**
Correct the cursor style of increment and decrement buttons in Safari.
*/
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
1. Correct the odd appearance in Chrome and Safari.
2. Correct the outline style in Safari.
*/
[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

/**
Remove the inner padding in Chrome and Safari on macOS.
*/
::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
1. Correct the inability to style clickable types in iOS and Safari.
2. Change font properties to 'inherit' in Safari.
*/
::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

/*
Interactive
===========
*/
/*
Add the correct display in Chrome and Safari.
*/
summary {
  display: list-item;
}

/*  --- reset css --- */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
body,
form,
aside,
input,
section,
article {
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

li {
  list-style-type: none;
}

input {
  outline: none;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  background-color: #505050;
}

/* --- typography --- */
@font-face {
  font-family: "Work Sans";
  src: url(../assets/webfonts/Font-face/WorkSans-Bold.woff2) format("woff2"), url(../assets/webfonts/Font-face/WorkSans-Bold.ttf) format("truetype");
  font-weight: 700;
}
@font-face {
  font-family: "Work Sans";
  src: url(../assets/webfonts/Font-face/WorkSans-SemiBold.woff2) format("woff2"), url(../assets/webfonts/Font-face/WorkSans-SemiBold.ttf) format("truetype");
  font-weight: 600;
}
@font-face {
  font-family: "Work Sans";
  src: url(../assets/webfonts/Font-face/WorkSans-Regular.woff2) format("woff2"), url(../assets/webfonts/Font-face/WorkSans-Regular.ttf) format("truetype");
  font-weight: 400;
}
/*   --- general settings --- */
html {
  box-sizing: border-box;
  font-size: var(--font-size);
  scroll-behavior: smooth;
}
html ::selection {
  background-color: #505050;
  color: white;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  background-color: #505050;
}

/*  --- header --- */
header {
  background-color: #ffffff;
}

.nav {
  max-width: var(--container);
  padding-inline: 2em;
  padding-block: 2em;
  margin-inline: auto;
  position: sticky;
  display: flex;
  gap: 1em;
}
.nav > * a {
  display: inline-block;
  font-size: 1.25rem;
  line-height: 1.5em;
  color: #000000;
  padding-inline: 0.5em;
  border: solid 1px transparent;
  border-radius: 5px;
  transition: ease-in-out 0.2s border, ease-in-out 0.2s background-color;
}
.nav > * a:hover, .nav > * a:focus {
  border-bottom: 1px solid black;
  border-top: 1px solid black;
  background-color: #00aeff;
}
.nav__1 {
  flex-grow: 1;
}
/* --- home main --- */
.home {
  font-optical-sizing: auto;
  background-color: #505050;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.container {
  max-width: var(--container);
  padding-inline: 2em;
  padding-block: 2em;
  margin-inline: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.who {
  background-color: #a7a7a7;
  border: 1px solid black;
  border-radius: 5px;
  padding: 2em;
}

.testing {
  max-width: var(--container);
  padding-inline: 2em;
  padding-block: 2em;
  margin-inline: auto;
}
.testing .top {
  display: flex;
  justify-content: center;
  font-size: 2rem;
  font-weight: 600;
  gap: 2em;
}
.testing .top > * {
  cursor: pointer;
  padding: 1em;
  background-color: rgb(137, 240, 128);
  border-radius: 5px;
}
.testing .top > *:hover {
  background-color: lightcoral;
}
.testing .result {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
  text-align: center;
  font-size: 3rem;
  color: white;
  font-weight: 800;
}

footer {
  background-color: #ffffff;
}

